.social__list {
  @include reset-list-style;
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-bottom: 25px;
}

.social__item {
  display: flex;
  justify-content: center;
  margin-right: 13.5%;

  &:last-child {
    margin-right: 0;
  }
}

.social__link {
  display: flex;
  align-items: flex-end;
  justify-content: center;
  width: 25px;

  svg {
    fill: #231f20;
    transition: all 0.3s ease;
  }

  &[href]:hover,
  &[href]:focus,
  &[href]:active {
    opacity: 1;
  }

  &[href]:hover,
  &[href]:focus {
    svg {
      fill: $very-dark-mint-color;
    }
  }

  &[href]:active {
    svg {
      fill: rgba($very-dark-mint-color, 0.3%);
    }
  }
}

@media (min-width: $tablet-width) {
  .social {
    width: 22%;
  }

  .social__list {
    margin-bottom: 0;
  }

  .social__item {
    margin-right: 35px;
  }
}
