.promo {
  min-height: 400px;
  padding-top: 74px;
  background-color: $bg-promo-gray;
  background-image: url("../img/hero-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position: 50% -20px;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/hero-mobile@2x.jpg");
    background-size: 575px auto;
  }
}

.promo__title {
  margin-bottom: 125px;
  font-size: 27px;
  line-height: 1.11;
  text-align: center;
}

.promo__list {
  @include reset-list-style;
  display: flex;
  flex-direction: column;
}

.promo__item {
  a {
    position: relative;
    width: 100%;
    height: 100%;
    padding: 26px 30px 30px;
    font-weight: 700;
    font-size: 19px;
    line-height: 1.26;
    color: $white-color;
    text-decoration: none;
    box-sizing: border-box;

    &[href]:hover,
    &[href]:focus,
    &[href]:active {
      opacity: 1;
    }
  }

  svg {
    position: absolute;
    top: 50%;
    right: 34px;
    transform: translateY(-50%);
    opacity: 0.5;
    fill: $dark-mint-color;
  }

  &--interior {
    a {
      background-color: $mint-color;

      &[href]:hover,
      &[href]:focus {
        box-shadow: 0 -5px 0 0 $mint-color;
      }
    }
  }

  &--kids-toys {
    a {
      background-color: $bg-mint;

      &[href]:hover,
      &[href]:focus {
        box-shadow: 0 -5px 0 0 $bg-mint;
      }
    }
  }
}

.promo__wrapper {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  overflow: hidden;

  &[href]:hover,
  &[href]:focus {
    svg {
      opacity: 0.7;
      top: calc(50% - 5px);
    }

    .promo__name-link {
      position: relative;
      top: -5px;
    }
  }

  &[href]:active {
    svg {
      opacity: 0.3;
    }

    .promo__name-link {
      opacity: 0.3;
    }
  }
}

.promo__item .promo__interior-icon {
  right: 35px;
  width: 40px;
  height: 62px;
}

.promo__toy-icon {
  width: 60px;
  height: 60px;
}

@media (min-width: $tablet-width) {
  .promo {
    padding-top: 152px;
    background-image: url("../img/hero-tablet@1x.jpg");
    background-position: 50% 0;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/hero-tablet@2x.jpg");
      background-size: 1150px;
    }
  }

  .promo__title {
    font-size: 43px;
    line-height: 1.16;
    margin-bottom: 375px;
  }

  .promo__title .br-mob {
    display: none;
  }

  .promo__list {
    flex-direction: row;
    width: 87%;
    max-width: 1050px;
    margin: 0 auto;
  }

  .promo__item {
    width: 50%;

    a {
      padding: 28px 35px;
      font-size: 20px;
      line-height: 1.5;
    }
  }
}

@media (min-width: $desktop-width) {
  .promo {
    padding-top: 155px;
    background-image: url("../img/hero-desktop@1x.jpg");

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/hero-desktop@2x.jpg");
    }
  }

  .promo__title {
    font-size: 53px;
    line-height: 1.08;
    margin-bottom: 356px;
  }

  .promo__list {
    width: 1050px;
  }

  .promo__item a {
    padding: 30px 35px 35px;
  }
}
